/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
* {
  box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

blockquote,
q {
  quotes: none;
}

blockquote:before, blockquote:after {
  content: "";
  content: none;
}

q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  max-width: 100%;
}

body,
html {
  background: #f7f8fc;
  font-family: "Inter", sans-serif;
  min-height: 100vh;
  font-size: 14px;
}

.application-wrap {
  width: 100%;
  min-height: 100vh;
  position: relative;
}

aside.application-nav {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  z-index: 3;
  width: 66px;
  background: #1d2b30;
}
aside.application-nav nav {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}
aside.application-nav nav ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  margin-top: 10px;
  list-style-type: none;
  padding: 0;
}
aside.application-nav nav ul button,
aside.application-nav nav ul a {
  border: none;
  text-decoration: none;
  display: block;
  background: none;
  color: #f7f8fc;
  width: 34px;
  cursor: pointer;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  border-radius: 3px;
  margin: 12px 0 12px;
}
aside.application-nav nav ul button i,
aside.application-nav nav ul a i {
  font-size: 22px;
}
aside.application-nav nav ul button:hover,
aside.application-nav nav ul a:hover {
  background: #0a0f10;
}
aside.application-nav nav ul button.is-selected,
aside.application-nav nav ul a.is-selected {
  background: rgba(255, 255, 255, 0.4);
}
aside.application-nav .company-logo {
  margin: 15px 0;
  display: block;
}

main.application-main-content {
  position: absolute;
  min-height: 100vh;
  width: calc(100% - 66px);
  top: 0;
  left: 66px;
}

header.application-header {
  position: fixed;
  background: #ffffff;
  height: 60px;
  width: calc(100% - 66px);
  padding: 10px 20px;
  display: flex;
  align-items: center;
  left: 66px;
  justify-content: space-between;
  filter: drop-shadow(0px 1px 0px rgba(0, 0, 0, 0.08));
}

section.application-content {
  top: 60px;
  position: relative;
  width: 100%;
  padding: 20px 20px 0px 30px;
}

.tippy-box[data-theme~=light] {
  background-color: #00a68f;
  color: #ffffff;
}

.tippy-box[data-theme~=light][data-placement^=top] > .tippy-arrow::before {
  border-top-color: #00a68f;
}

.tippy-box[data-theme~=light][data-placement^=bottom] > .tippy-arrow::before {
  border-bottom-color: #00a68f;
}

.tippy-box[data-theme~=light][data-placement^=left] > .tippy-arrow::before {
  border-left-color: #00a68f;
}

.tippy-box[data-theme~=light][data-placement^=right] > .tippy-arrow::before {
  border-right-color: #00a68f;
}

.sidebar-new, .offcanvas-panel {
  position: fixed;
  left: 66px;
  width: 240px;
  background-color: white;
  height: calc(100vh - 61px);
  top: 61px;
  transform: translateX(-240px);
  transition: all ease-in 0.3s;
  z-index: 2;
}
.sidebar-new .sidebar-new__heading, .offcanvas-panel .sidebar-new__heading {
  padding: 20px 15px;
  position: relative;
}
.sidebar-new .sidebar-new__heading h2, .offcanvas-panel .sidebar-new__heading h2 {
  font-weight: 600;
  margin: 1px 0 0;
  padding: 0;
  line-height: 1;
  font-size: 14px;
  text-transform: uppercase;
}
.sidebar-new .sidebar-new__content, .offcanvas-panel .sidebar-new__content {
  padding: 0 15px 20px;
  overflow-y: auto;
  height: calc(100% - 55px);
}
.sidebar-new .sidebar-new__content::-webkit-scrollbar-track, .offcanvas-panel .sidebar-new__content::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.013);
  border-radius: 10px;
  background-color: #f5f5f5;
}
.sidebar-new .sidebar-new__content::-webkit-scrollbar, .offcanvas-panel .sidebar-new__content::-webkit-scrollbar {
  width: 8px;
  background-color: #f5f5f5;
}
.sidebar-new .sidebar-new__content::-webkit-scrollbar-thumb, .offcanvas-panel .sidebar-new__content::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: none;
  background-color: #7c7c7c;
}
.sidebar-new .sidebar-new__content h3, .offcanvas-panel .sidebar-new__content h3 {
  display: flex;
  font-size: 18px;
  line-height: 1;
  align-items: center;
  margin: 0 0 25px;
}
.sidebar-new .sidebar-new__content h3 small, .offcanvas-panel .sidebar-new__content h3 small {
  margin-left: 12px;
  height: 20px;
  width: 30px;
  background: #18bbad;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  border-radius: 30px;
}

.close-sidebar-new {
  position: absolute;
  right: 0;
  top: 15px;
  border: none;
  border-radius: 100px;
  width: 26px;
  height: 26px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  transition: all ease-in 0.2s;
  background: #1d2b30;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  z-index: 2;
}
.close-sidebar-new:hover {
  background: #00a68f;
  width: auto;
}
.close-sidebar-new span {
  line-height: 1;
}

.offcanvas-panel.offcanvas {
  transform: translateX(0) !important;
  transition: all ease-out 0.3s;
  border-right: 1px solid #e4e5e9;
}
.offcanvas-panel.offcanvas .close-sidebar-new {
  right: -13px;
}
.offcanvas-panel .app-sidebar__inner {
  padding: 20px;
  min-height: 100%;
}

body {
  overflow-x: hidden;
}
body.no-scrolling {
  overflow: hidden;
}
body.no-scrolling .sidemenu-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.123);
  display: block;
  z-index: 9;
}

.sidemenu-overlay {
  display: none;
}

.left-margin {
  margin-left: 241px;
  transition: all ease-out 0.3s;
}

.no-margin {
  margin-left: 0;
  transition: all ease-out 0.3s;
}

.left-nav {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.left-nav li {
  position: relative;
}
.left-nav li .is-expanded {
  position: relative;
}
.left-nav li .is-expanded:after {
  content: "\ea13";
  font-family: remixicon !important;
  position: absolute;
  right: 10px;
  font-size: 16px;
  top: 50%;
  transform: translateY(-50%);
}
.left-nav li .is-expanded.menu-expanded {
  font-weight: 600;
  background: #f3f3f3;
  color: #00a68f;
}
.left-nav li .is-expanded.menu-expanded:after {
  content: "\f1ae";
}
.left-nav li ul.sub-nav {
  margin: 0;
  padding: 5px 0 10px;
  list-style-type: none;
  display: none;
}
.left-nav li ul.sub-nav a {
  font-size: 13px;
  padding-top: 8px;
  padding-bottom: 8px;
  position: relative;
  padding-left: 18px;
  color: #57808f;
}
.left-nav li ul.sub-nav a:before {
  content: "\ea53";
  font-family: remixicon !important;
  position: absolute;
  left: 0;
  font-size: 16px;
  top: 50%;
  transform: translateY(-50%);
}
.left-nav li ul.sub-nav a:hover {
  background: none;
  color: #00a68f;
}
.left-nav a {
  display: block;
  padding: 10px 10px;
  color: #1d2b30;
  text-decoration: none;
  transition: all ease-in 0.2s;
}
.left-nav a:hover {
  border-radius: 3px;
  background: #f3f3f3;
}